export default {
	data() {
		return {
			curPage: "1",
			showNewPop: false,
		}
	},
	methods: {
		NavChange: function(e) {
			this.curPage = e.currentTarget.dataset.cur
			this.showNewPop = false
		},
		onAddClick: function(message) {
			console.log(message)
			// this.curPage = "asd"
			if(!this.showNewPop){
				this.showNewPop = true
			} else {
				this.showNewPop = false
			}
		}
	}
}
